@import "style/mixins.module.scss";

.text {
    &_with-label {
        margin-bottom: 8px;
    }

    @include respond-to("laptop") {
        font-size: 22px;
        line-height: 32px;
    }
}

.label {
    opacity: 0.3;

    &_is-active {
        opacity: 1;
    }
}

@media (min-width: 1024px) {
    .root {
        flex-grow: 1;
        flex-basis: 0;
        margin-right: 24px;
        cursor: pointer;

        &:last-child {
            margin-right: 0;
        }
    }

    .description {
        padding-top: 16px;
        color: #abb4bb;
        font-size: 22px;
        line-height: 32px;

        transition: color 0.2s;

        &_is-active {
            color: #424e58;
        }
    }

    .meter {
        height: 4px;
        position: relative;
        background: #fdefef;
        overflow: hidden;
    }

    .progress-container {
        display: none;
        height: 100%;

        &_is-active {
            display: block;
        }
    }

    .progress {
        position: absolute;
        height: 100%;
        background-color: #eb5e5b;
        animation-name: progressBar;
        animation-timing-function: linear;
        animation-fill-mode: both;
        animation-iteration-count: infinite;
    }

    @keyframes progressBar {
        from {
            width: 0;
        }

        to {
            width: 100%;
        }
    }
}
