@import "style/mixins.module.scss";

.root {
    margin-right: 8px;

    &:last-child {
        margin-right: 24px;
    }

    &_margin-bottom {
        margin-right: 0;
        margin-bottom: 24px;

        &:last-child {
            margin-right: 0;
            margin-bottom: 0;
        }
    }
}

.title {
    margin-bottom: 8px;
}

.count {
    margin-right: 10px;
    font-size: 17px;
    line-height: 24px;
    font-weight: 700;

    @include color("primary", "red");
}

.label {
    @include respond-to("laptop") {
        display: none;
    }
}

.image-content {
    width: 230px;
    height: 180px;
    margin-bottom: 16px;

    &:last-child {
        margin-bottom: 0;
    }

    &_full-width {
        width: 100%;
        height: 240px;
    }
}

.image {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.content {
    &_flex {
        display: flex;
        align-items: flex-start;
    }

    @include respond-to("laptop") {
        display: none;
    }
}

.description {
    font-size: 17px;
    line-height: 24px;
    margin-bottom: 0;

    &_with-label {
        margin-bottom: 8px;
    }
}

@media (min-width: 1024px) {
    .root {
        margin: 0;

        &:last-child {
            margin-right: 0;
        }
    }

    .image-content {
        width: 100%;
        height: 100%;
    }

    .image {
        opacity: 0;
        display: none;
        object-fit: cover;
        object-position: left;
        width: 560px;
        height: 560px;

        &_is-active {
            display: block;
        }
    }

    .image {
        animation-name: progressImage;
        animation-timing-function: ease-out;
        animation-fill-mode: both;
        animation-duration: 1s;
    }

    @keyframes progressImage {
        from {
            display: none;
            opacity: 0;
        }

        to {
            display: block;
            opacity: 1;
        }
    }

    .description {
        display: none;
    }
}
