.root {
    padding-right: 24px;
    padding-left: 24px;
}

.actions-controls {
    display: none;
}

.actions-info {
    display: block;
}

.action-consult {
    margin-right: 8px;
}

@media (min-width: 1024px) {
    .root {
        display: flex;
        flex-grow: 1;
        align-items: flex-end;
        justify-content: space-between;
        padding: 0;
        width: 100%;
    }

    .actions-info,
    .actions-controls {
        display: flex;
    }

    .action-navigate {
        width: 56px;
        height: 56px;
        outline: 1px solid rgba(66, 78, 88, 0.1);
        background-color: transparent;
        border: none;
        cursor: pointer;

        &:first-child {
            margin-right: 8px;
        }
    }
}
