@import "style/mixins.module.scss";

.title {
    margin-bottom: 24px;

    @include respond-to("laptop") {
        margin-bottom: 40px;
    }
}

.actions-desktop {
    display: none;

    @include respond-to("laptop") {
        display: flex;
    }
}

.actions-mobile {
    display: block;

    @include respond-to("laptop") {
        display: none;
    }
}

.description {
    margin-bottom: 24px;

    font-size: 17px;
    line-height: 24px;
}

.info-stages {
    padding-right: 24px;
    padding-left: 24px;

    &_no-padding {
        padding-left: 0;
        padding-right: 0;
    }
}

.stages-images-desktop {
    display: none;
}

@media (min-width: 1024px) {
    .root {
        display: flex;
        max-height: 600px;
    }

    .column {
        display: flex;
    }

    .column-content {
        width: 60%;
        margin-right: 80px;
        flex-direction: column;
        align-items: flex-end;
    }

    .column-images {
        width: 560px;
    }

    .content {
        max-width: 712px;
        height: 100%;

        display: flex;
        flex-direction: column;
    }

    .description {
        margin-bottom: 40px;

        font-size: 22px;
        line-height: 32px;
    }

    .info-stages {
        padding: 0;
    }

    .stages-images-desktop {
        display: inherit;
    }
}
