@import "style/mixins.module.scss";

.root {
    font-size: 17px;
    line-height: 24px;
    font-weight: 400;
}

.title {
    margin-bottom: 16px;

    @include respond-to("laptop") {
        max-width: 730px;
        margin-bottom: 24px;
    }
}

.description {
    margin-bottom: 24px;

    @include respond-to("laptop") {
        max-width: 730px;
        margin-bottom: 64px;

        font-size: 22px;
        line-height: 32px;
    }
}

.list {
    padding-left: 0;
    margin: 0;
    margin-bottom: 32px;

    @include respond-to("laptop") {
        display: flex;
        align-items: flex-start;
        justify-content: center;
        width: 100%;
        margin-bottom: 0;
    }
}

.item {
    margin-bottom: 16px;
    display: flex;

    &:last-child {
        margin-bottom: 0;
    }

    &__icon {
        margin-right: 20px;

        @include color("primary", "red");

        @include respond-to("laptop") {
            display: none;
        }
    }

    &__text {
        margin-bottom: 0;

        @include respond-to("laptop") {
            font-size: 22px;
            line-height: 32px;
        }
    }

    &__image {
        display: none;

        @include respond-to("laptop") {
            object-fit: cover;
            width: 100%;
            height: 280px;
            margin-bottom: 16px;
            display: block;
        }
    }

    @include respond-to("laptop") {
        display: block;
        margin-bottom: 0;
        margin-right: 40px;
        width: 376px;

        &:last-child {
            margin-right: 0;
        }
    }
}

.image {
    display: block;
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.container-image {
    @include respond-to("laptop") {
        display: none;
    }
}
